import { h } from 'vue';

export default {
    // functional: true,

    props:
    {
        item: Object,
    },

    render()
    {
        return h(
            'div',

            {
                // staticClass: data.staticClass,

                class: [
                    'vh-badge',
                    'vh-badge-primary',
                    'vh-flex-middle',
                    'vh-badge-margin-small-top',
                ],
            },

            [
                h(
                    'div',

                    [
                        h(
                            'i',
                            {
                                class: [
                                    `fa ${this.item.icon}`,
                                    'vh-margin-xsmall-right',
                                ]
                            }
                        ),
                        this.item.value,
                    ],
                ),

                h(
                    'div',
                    {
                        class: 'vh-margin-small-left',
                    },

                    [
                        h(
                            'a',
                            {
                                class: 'vh-link-reset vh-point',
                            },
                            [
                                h(
                                    'i',
                                    {
                                        class: 'fa fa-times',

                                        onClick: () => this.$emit('reset'),
                                    },
                                ),
                            ]
                        ),
                    ]
                ),
            ]
        );
    },
};
