import { Storage } from '@/utils';

export const auth = {

    namespaced: true,

    state:
    {
        name: 'Maria Clara',

        series:
        {
            starting_no: 100000,
            ending_no: 100099,
            total_no: 100,
            last_release_no: null,
            is_set: false,
        },

        isActive: false,

        userLoading: false,
        registerLoading: false,
        saveLoading: false,
        updateLoading: false,
        removeLoading: false,
    },

    mutations:
    {
        setUser: (state, data) =>
        {
            state.name = data.name;
            state.series = data.series;
            state.isActive = true;
        },

        setName: (state, data) =>
        {
            state.name = data;
        },

        setSeriesNo: (state, { action, value }) =>
        {
            state.series[`${action}_no`] = value;
        },

        updateIsSet: (state, bool) =>
        {
            state.series.is_set = bool;
        },

        setLoading: (state, { action, boolean }) =>
        {
            state[`${action}Loading`] = boolean;
        },

        resetState: (state) =>
        {
            state.name = 'Maria Clara';
            state.starting_no = 100000;
            state.ending_no = 100099;
            state.total_no = 100;
            state.last_release_no = null;
            state.is_set = false;
            state.isActive = false;
        },
    },

    actions:
    {
        register: ({ commit }, name) => 
		{
			commit('setLoading', { action: 'user', boolean: true });

            Storage.set(process.env.VUE_APP_LOCAL_STORAGE_NAME, name);

            const series = {
                starting_no: 100000,
                ending_no: 100099,
                total_no: 100,
                last_release_no: null,
                is_set: false,
            };

            Storage.set(process.env.VUE_APP_LOCAL_STORAGE_LIST, JSON.stringify(series));

			commit('setUser', {
                name: name,
                series: series
            });
			
			commit('setLoading', { action: 'user', boolean: false });
		},

        saveName: ({ commit, getters }, name) => 
		{
			commit('setLoading', { action: 'save', boolean: true });

            commit('setName', name);

            Storage.destroy(process.env.VUE_APP_LOCAL_STORAGE_NAME);

            Storage.set(process.env.VUE_APP_LOCAL_STORAGE_NAME, getters.name);
			
			commit('setLoading', { action: 'save', boolean: false });
		},

        saveSeries: ({ commit, getters }, data) => 
		{
			commit('setLoading', { action: 'save', boolean: true });

            commit('setSeriesNo', { action: 'starting', value: data.starting_no });

            commit('setSeriesNo', { action: 'ending', value: data.ending_no });

            commit('setSeriesNo', { action: 'total', value: data.total_no });

            commit('setSeriesNo', { action: 'last_release', value: data.last_release_no });

            commit('updateIsSet', true);

            Storage.destroy(process.env.VUE_APP_LOCAL_STORAGE_LIST);

            Storage.set(process.env.VUE_APP_LOCAL_STORAGE_LIST, JSON.stringify(getters.series));
			
			commit('setLoading', { action: 'save', boolean: false });
		},

        updateLastRelease: async ({ commit, getters }, data) => 
		{
			commit('setLoading', { action: 'update', boolean: true });

            await commit('setSeriesNo', { action: 'last_release', value: data });

            Storage.destroy(process.env.VUE_APP_LOCAL_STORAGE_LIST);

            Storage.set(process.env.VUE_APP_LOCAL_STORAGE_LIST, JSON.stringify(getters.series));
			
			commit('setLoading', { action: 'update', boolean: false });
		},

        loadUser: ({commit}, { name, series }) => 
		{
			commit('setLoading', { action: 'user', boolean: true });

			commit('setUser', { name, series });
			
			commit('setLoading', { action: 'user', boolean: false });
		},

        forceSignout: async () =>
        {
            await Storage.destroy(process.env.VUE_APP_LOCAL_STORAGE_NAME);

            await Storage.destroy(process.env.VUE_APP_LOCAL_STORAGE_LIST);
        },

        unset: ({ commit }) =>
        {
            commit('resetState');
        },
    },

    getters:
    {
        name: state => state.name,

        series: state => state.series,

        getSeriesNo: state => action => state.series[`${action}_no`],

        isSet: state => state.series.is_set,

        isActive: state => state.isActive,

        isLoading: state => action => state[`${action}Loading`],
    },
};
