<template>
    <div class="vh-text-center">
        <p class="vh-margin-remove">
            
        </p>
        <h1 class="axs-title vh-margin-medium-top">
            Hi, Ticketr!
        </h1>

        <div class="vh-margin-large-top">
            <form class="vh-form-stacked"
                @submit.prevent="submit()">
                <fieldset class="vh-fieldset">
                    <div class="vh-margin">
                        <label class="axs-input-label">
                            What is your nickname?
                        </label>
                        <input
                            type="text"
                            class="axs-input"
                            required="true"
                            autofocus="true"
                            placeholder="ex. MARIA CLARA"
                                v-model="form.name">

                        <small class="vh-text-danger vh-text-bold"
                            v-if="hasError('name')">
                            {{ errors.name[0]}}
                        </small>
                    </div>
                </fieldset>

                <div class="vh-margin-top">
                    <vh-button
                        type="submit"
                        :size="'large'"
                        :color="'default'"
                        :fullWidth="true">
                        🚀 LET'S START
                    </vh-button>
                </div>
            </form>
        </div>

        
    </div>
</template>

<script>
import { mapActions } from 'vuex';

import { Form, Toast } from '@/utils';

export default
{
    name: 'Register',

    data()
    {
        return {

            errors: {},

            form:
            {
                name: 'MARIA CLARA'
            },
        }
    },

    methods:
    {
        ...mapActions({
            register: 'auth/register',
        }),

        submit()
        {
            this.errors = {};

            if (!this.form.name)
            {
                this.errors.name= ['😌 I would like to know you, atleast your nickname?'];

                Toast.error({
                    message: 'Oops, kindly double check your form.',
                });
            }

            this.register(this.form.name);

            this.$router.push('/tickets');
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },
    }
};
</script>
