import { createApp } from 'vue';

import App from './App';
import Toaster from "@meforma/vue-toaster";

import { router } from './router';
import { store } from './stores';
import { Storage, Toast } from './utils';

import BaseComponents from  './components';

if (Storage.get(process.env.VUE_APP_LOCAL_STORAGE_NAME) !== null)
{
    store.dispatch(
        'auth/loadUser',
        {
            name: Storage.get(process.env.VUE_APP_LOCAL_STORAGE_NAME),
            series: Storage.get(process.env.VUE_APP_LOCAL_STORAGE_LIST)
                ? JSON.parse(Storage.get(process.env.VUE_APP_LOCAL_STORAGE_LIST))
                : {
                    starting_no: 100000,
                    ending_no: 100099,
                    total_no: 100,
                    last_release_no: null,
                    is_set: false,
                }
        }
    );
}

const app = createApp(App)
    .use(Toaster)
    .use(store)
    .use(router);

BaseComponents.register(app);
Toast.register(app);

app.mount('#app')