import { h } from 'vue';

export default {
    // functional: true,

    props:
    {
        label:
        {
            type: String,
            default: null,
        },
    },

    render()
    {
        return h(
            'div',
            {
                // staticClass: data.staticClass,

                class:
                [
                    'vh-card',
                    'vh-card-body',
                    'vh-padding',
                    'vh-text-center',
                ],
            },
            [
                h(
                    'h3',

                    this.label
                ),

                h(
                    'img',

                    {
                        class:
                        [
                            'vh-margin-top',
                        ],
                        src: '/assets/image/illustrations/professional.png',
                        alt: 'Empty Illustration',
                        width: '800',
                        height: '800',
                    },
                ),
            ]
        );
    },
};
