// import { createApp } from 'vue';
import { createRouter, createWebHashHistory } from "vue-router";

import { Storage } from '@/utils';

import routes from './routes';

export const router = createRouter({
    history: createWebHashHistory(),
    root: '/',
    routes,
});

// export const router = new Router({
//     mode: 'history',
//     transitionOnLoad: true,
//     // linkActiveClass: 'active',
//     linkExactActiveClass: 'mm-active',
//     root: '/',
//     routes,
// });

router.beforeEach((to, from, next) =>
{
    const notAuthRequired = ['register'];
	const authRequired = !notAuthRequired.includes(to.meta.page);

    const hasName = Storage.get(process.env.VUE_APP_LOCAL_STORAGE_NAME) !== null;

    // if (hasAccessToken && to.path === '/') return next('/dashboard');

    if(authRequired && !hasName) return next('/register');

	if(!authRequired && hasName) return next('/');

	if(to.path == '/') return next('/tickets');

    return next();
});
